@width: 14px;
@styleName: index-style;

.@{styleName} {
    font-size: @width;
    color: aqua;

    &-head {
        display: flex;
        position: fixed;
        top: 0;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        height: 40px;
        line-height: 40px;
        &-left {
            width: 40px;
            text-align: center;
        }

        &-right {
            width: 40px;
            text-align: center;
        }
    }
}