.header-style {
  width: 100%;
  height: 40px;
}
.header-style-head {
  display: flex;
  position: fixed;
  top: 0;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
}
.header-style-head-left {
  width: 40px;
  text-align: center;
}
.header-style-head-right {
  width: 40px;
  text-align: center;
}
